@import url("https://fonts.googleapis.com/css?family=Press+Start+2P");
@import url("https://unpkg.com/nes.css/css/nes.css");

body {
  background: #ebebeb;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: "Press Start 2P";
}

.App {
  height: 400px;
  width: 300px;
  background: #ebebeb;
  position: relative;
}

.App:hover::before {
  opacity: 0;
  height: 0;
}

.App:hover::after {
  opacity: 0;
  width: 0;
}

.Display {
  background: black;
  color: white;
  text-shadow: 0 0 5px rgba(89, 195, 195, 0.5);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 29px;
  box-sizing: border-box;
  height: 20%;
  overflow: hidden;
  font-size: 24px;
  position: relative;
}

.Display::after {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,f1f1f1+50,e1e1e1+51,f6f6f6+100;White+Gloss+%231 */
  background: white;
  /* Old browsers */
  background: -moz-linear-gradient(
    top,
    white 0%,
    #f1f1f1 50%,
    #e1e1e1 51%,
    #f6f6f6 100%
  );
  /* FF3.6-15 */
  background: -webkit-linear-gradient(
    top,
    white 0%,
    #f1f1f1 50%,
    #e1e1e1 51%,
    #f6f6f6 100%
  );
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to bottom,
    white 0%,
    #f1f1f1 50%,
    #e1e1e1 51%,
    #f6f6f6 100%
  );
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#f6f6f6', GradientType=0);
  /* IE6-9 */
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  pointer-events: none;
  height: 100%;
  mix-blend-mode: overlay;
  display: block;
  z-index: 2;
}

.Buttons {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  height: 80%;
}

.Button {
  background: #fff;
  /* Old browsers */
  /* background: -moz-linear-gradient(45deg, #cad2c5 0%, #abb5aa 100%); */
  /* FF3.6-15 */
  /* background: -webkit-linear-gradient(45deg, #cad2c5 0%, #abb5aa 100%); */
  /* Chrome10-25,Safari5.1-6 */
  /* background: linear-gradient(45deg, #cad2c5 0%, #abb5aa 100%); */
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  /* filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cad2c5', endColorstr='#abb5aa', GradientType=1); */
  /* IE6-9 fallback on horizontal gradient */
  display: flex;
  position: relative;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-image-slice: 2;
  border-image-width: 2;
  border-image-repeat: stretch;
  border-image-outset: 2;
  align-items: center;
  justify-content: center;
  width: 25%;
  font-size: 24px;
  color: #212529;
  height: 20%;
  min-width: 25%;
}

.Button[data-size="2"] {
  height: 40%;
}

.Button[data-value="null"] {
  pointer-events: none;
}

.Button:hover {
  background: #adafbc;
  cursor: pointer;
}

.Button:active {
  background: #bdc7b7;
}
